import "@servicenow/now-button";

export const view = ({ properties }) => {
	const { label, disabled, position } = properties;
	if (label)
		return (
			<div
				className="catalog-action-button"
				style={{
					textAlign: position
				}}
			>
				<now-button
					label={label}
					disabled={disabled}
					size="md"
					variant="primary"
				/>
			</div>
		);
};
