import { createCustomElement } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import { t } from "sn-translate";
import { actions, actionHandlers } from "./actions";
import { view } from "./view";

/**
 * Action Button renders a button which can be labelled(ex: Submit,Request) and disabled using label and disbaled properties.
 *You can align the button using the position property
 * ```jsx
 * <sn-catalog-action label="Submit" disabled="false" position="center">
 * </sn-catalog-action>
 * ```
 *
 * @seismicElement sn-catalog-action
 * @summary Action button for catalog item
 */

createCustomElement("sn-catalog-action", {
	renderer: { type: snabbdom },
	view,
	properties: {
		/**
		 * label of the action button
		 *
		 * @type {string}
		 */
		label: {
			default: t("Submit")
		},
		/**
		 * Disabled property disables(makes readonly) the button when set true
		 *
		 * @type {boolean}
		 */
		disabled: {
			default: false
		},
		/**
		 *	Defines the position of the button,can be left,right or center
		 *
		 * @type {string}
		 */
		position: {
			default: "left"
		}
	},
	actions,
	actionHandlers,
	dispatches: {
		/**
		 * Dispatched when submit button clicked
		 * @type {{}}
		 */
		SUBMIT_CLICKED: {}
	}
});
